import React, { Suspense, useState, useEffect, useLayoutEffect } from 'react';
import { Card, CardContent, CardHeader, Typography, CircularProgress } from "@mui/material";
import { 
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend,
  LineChart, Line, PieChart, Pie, Cell,
  ResponsiveContainer 
} from 'recharts';

// Chart wrapper component with fixed resize handling
const SafeChart = ({ children }) => {
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });
  const [isReady, setIsReady] = useState(false);

  useLayoutEffect(() => {
    setIsReady(true);
    
    const handleResize = () => {
      // Use RAF to throttle resize updates
      requestAnimationFrame(() => {
        setDimensions({
          width: window.innerWidth,
          height: window.innerHeight
        });
      });
    };

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  if (!isReady) {
    return (
      <div className="h-64 flex items-center justify-center">
        <CircularProgress size={24} />
      </div>
    );
  }

  return (
    <div className="h-64 w-full relative">
      {children}
    </div>
  );
};

const ReviewDashboard = () => {
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    return () => setMounted(false);
  }, []);

  // Sample data processing
  const reviews = [
    {"reviewerID":"A3SBTW3WS4IQSN","overall":4.0,"day_diff":138,"helpful_yes":0,"total_vote":0},
    {"reviewerID":"A18K1ODH1I2MVB","overall":5.0,"day_diff":409,"helpful_yes":0,"total_vote":0},
    {"reviewerID":"A2FII3I2MBMUIA","overall":4.0,"day_diff":715,"helpful_yes":0,"total_vote":0},
    {"reviewerID":"A3H99DFEG68SR","overall":5.0,"day_diff":382,"helpful_yes":0,"total_vote":0},
    {"reviewerID":"A375ZM4U047O79","overall":5.0,"day_diff":513,"helpful_yes":0,"total_vote":0}
  ];

  // Calculate metrics
  const averageRating = reviews.reduce((acc, curr) => acc + curr.overall, 0) / reviews.length;
  const totalReviews = reviews.length;

  // Prepare data for rating distribution
  const ratingDistribution = Array.from({length: 5}, (_, i) => ({
    rating: i + 1,
    count: reviews.filter(r => r.overall === i + 1).length
  }));

  // Time series data
  const timeData = [
    { month: 'Jan', reviews: 42 },
    { month: 'Feb', reviews: 35 },
    { month: 'Mar', reviews: 50 },
    { month: 'Apr', reviews: 45 },
    { month: 'May', reviews: 55 },
    { month: 'Jun', reviews: 48 }
  ];

  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8'];
  const pieData = [
    { name: '5 Stars', value: reviews.filter(r => r.overall === 5).length },
    { name: '4 Stars', value: reviews.filter(r => r.overall === 4).length },
    { name: '3 Stars', value: reviews.filter(r => r.overall === 3).length },
    { name: '2 Stars', value: reviews.filter(r => r.overall === 2).length },
    { name: '1 Star', value: reviews.filter(r => r.overall === 1).length }
  ];

  if (!mounted) {
    return (
      <div className="w-full h-screen flex items-center justify-center">
        <CircularProgress />
      </div>
    );
  }

  return (
    <div className="w-full p-4 space-y-4">
      {/* Header Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card elevation={2}>
          <CardHeader title="Average Rating" />
          <CardContent>
            <Typography variant="h4">{averageRating.toFixed(1)}/5.0</Typography>
          </CardContent>
        </Card>
        
        <Card elevation={2}>
          <CardHeader title="Total Reviews" />
          <CardContent>
            <Typography variant="h4">{totalReviews}</Typography>
          </CardContent>
        </Card>
        
        <Card elevation={2}>
          <CardHeader title="5-Star Reviews" />
          <CardContent>
            <Typography variant="h4">
              {((reviews.filter(r => r.overall === 5).length / totalReviews) * 100).toFixed(1)}%
            </Typography>
          </CardContent>
        </Card>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Card elevation={2}>
          <CardHeader title="Rating Distribution" />
          <CardContent>
            <SafeChart>
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={ratingDistribution} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="rating" />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="count" fill="#8884d8" />
                </BarChart>
              </ResponsiveContainer>
            </SafeChart>
          </CardContent>
        </Card>

        <Card elevation={2}>
          <CardHeader title="Reviews Over Time" />
          <CardContent>
            <SafeChart>
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={timeData} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="month" />
                  <YAxis />
                  <Tooltip />
                  <Line type="monotone" dataKey="reviews" stroke="#8884d8" />
                </LineChart>
              </ResponsiveContainer>
            </SafeChart>
          </CardContent>
        </Card>

        <Card elevation={2}>
          <CardHeader title="Rating Distribution (Pie)" />
          <CardContent>
            <SafeChart>
              <ResponsiveContainer width="100%" height="100%">
                <PieChart margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
                  <Pie
                    data={pieData}
                    cx="50%"
                    cy="50%"
                    outerRadius={80}
                    fill="#8884d8"
                    dataKey="value"
                    label
                  >
                    {pieData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip />
                  <Legend />
                </PieChart>
              </ResponsiveContainer>
            </SafeChart>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default ReviewDashboard;